@import url('https://fonts.googleapis.com/css2?family=DynaPuff:wght@500&family=Inter:wght@400;600;700&family=Jost:ital,wght@1,300&family=Lato:ital,wght@1,300&family=Merriweather&family=Noto+Sans:ital,wght@1,300&family=Open+Sans:ital,wght@0,300;0,400;1,600&family=Oswald:wght@500&family=Piazzolla:ital,opsz,wght@1,8..30,300&family=Poppins:wght@300;400&family=Raleway:wght@300&family=Roboto+Condensed:ital,wght@1,300&family=Roboto:ital,wght@0,300;0,700;1,100&family=Rubik+Vinyl&family=Ubuntu:wght@700&display=swap');
*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: 'Poppins', sans-serif;
}

a{
  text-decoration: none;
  color: black;
  margin: 20px;
}

.admin_route > a{
  text-decoration: none;
  margin: 0;
  color: black;
  margin:2px;
}

.admin-link{
  text-decoration: none;
  color: black;
}

#main_div{
  font-size: 2.1rem;
  color: tomato;
  font-weight: bolder;
  font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
}

.navbar {
  font-size: 17px;
  line-height: 26px;
  text-transform: uppercase;
  box-shadow: 0px 8px 6px -6px gray;
  --webkit-box-shadow:0 8px 6px -6px gray;
  border-bottom: 1px solid gray !important;
}



.nav-link{
  font-weight: normal !important;

}

.active{
  color: red;
  font-weight: bold;
}

.footer{
  color: white;
  background: linear-gradient(260deg,black,gray);
  padding: 25px;
}

.footer > h4{
  color: tomato;
  font-weight: bolder;
}

.footer > p > a{
  color: white;
}
.pnf{
  display: flex;
  height: 76vh;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.pnf > a{
  border: 2px solid black;
  padding: 1rem 3rem;
  background-color: tomato;
  color: white;
  border: none;
  border-radius: 20px;
}

.pnf > .pnf-title{
  font-size: 8rem;
  font-weight: bolder;
}
.register{
  display: flex;
  align-items: center;
  justify-content: center;
  min-height: 70vh;
  flex-direction: column;
}

input{
  outline: none;
}

.conatiner{
  height: 100vh;
  width: 100vw;
  display: flex;
  align-items: center;
  justify-content: center;
}



@media screen and (max-width:600px){
  .cart_div{
    width: 100%;
    display: flex;
    align-items: center;
  }
  .cart_items{
    width: 100%;
  }
.cart_img{
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
}

.cart_image{
  width: 100%;
  /* margin-left: 6rem; */
}

  .cart_details > h4,p{
  text-align: center;
  }

 .price_span{
    font-size: 0.6rem
  }
  .cart_button{
    text-align: center;
    margin-left: 9em
}
}